import React, { useState, useEffect } from "react";
import * as serviceWorker from '../serviceWorker';
import styles from "./AppUpdate.module.scss";

function useAppUpdateAvailable() {
  if (!("serviceWorker" in navigator)) {
    return false;
  }
  const [updateAvailable, setupdateAvailable] = useState(false);
  useEffect(() => {
    let ignore = false;
    window.navigator.serviceWorker.getRegistration().then(registration => {
      if (registration && registration.waiting && !ignore) {
        console.log("New App waiting");
        setupdateAvailable(true);
      }
    });
    return () => {
      ignore = true;
    };
  }, []);
  return updateAvailable;
}

export default function AppUpdate() {
  const appUpdateAvailable = useAppUpdateAvailable();
  if (!appUpdateAvailable) return null;
  const reloadApp = () => {
    serviceWorker.unregister();
    window.location.reload();
  };
  return (
    <div className={styles.floatingContainer}>
      <article className={"message is-primary " + styles.withShadow}>
        <div className="message-header">
          <button
            className="button is-primary is-inverted is-outlined"
            onClick={reloadApp}
          >
            Update Now
          </button>
          <p>New version of the app available!!!</p>
          <button className="delete is-medium" aria-label="delete" />
        </div>
        <div className="message-body">
          New version will automatically load after all windows reopened.
        </div>
      </article>
    </div>
  );
}
